import React from 'react';

const styles = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '100%',
    fontSize: '24px',
    color: '#BABABA',
    fontFamily: 'Dosis, sans-serif'
}

const Inprogress = () => (
    <div style={styles}>This section is in progress...</div>
);

export default Inprogress;



