import React from 'react';
import './home.css';
import {getDate} from './date';

const Home = () => (
    <div className="home">
        <div className="home__content">
            <p className="home__greeting">Hello, I'm</p>
            <p className="home__name">Sergei Morozov</p>
            <p className="home__description">I have {getDate()} of experience as Junior Frontend Developer</p>
        </div>
    </div>
);

export default Home;



