import moment from 'moment';

const start = moment([2019, 6, 17]);
const months = Math.ceil(moment().diff(start, 'months', true))%12;
const years = Math.floor(moment().diff(start, 'years', true));

export const getDate = () => {
  let date = '';
  if (years > 1) {
      date += years + ' years ';
  }
  if ( years === 1) {
      date += years + ' year ';
  }
  if (months > 1) {
      date += months + ' months ';
  }
  if (months === 1) {
      date += months + ' month ';
  }
  return date;
};



