import React from 'react';
import Section from './section';
import './about.css';

const data = [
    {
        date: 'July 2019 — currently',
        name: 'Inanomo',
        text: 'Creation of user interfaces. Writing a RESTful API of applications in collaboration with back-end developers. Refactoring and optimizing legacy code. Correction of different problems and errors.'
    }
]

const About = () => (
    <div className="wrapper about">
        <h2 className="about__header">Experience</h2>
        {data.map(x => (
            <Section 
                date={x.date}
                name={x.name}
                text={x.text}
            />
        ))}
    </div>
);

export default About;


